.form {
  display: flex;
  justify-content: center;
  /* flex-direction: column; */
  margin-bottom: 3vh;
}

.form > input {
  width: fit-content;
  width: 50%;
  height: 6.5vh;
  border-radius: 6px;
  margin: 5px;
  text-indent: 10px;
}

.form > button {
  width: fit-content;
  width: 10%;
  height: 7vh;
  border-radius: 7px;
  margin: 5px;
  margin-top: 6px;
}

input[type="text"] {
  background-color: rgb(40, 44, 52);
  border-color: white;
  font-size: 90%;
  color: white;
}

.form > button {
  height: auto;
  min-width: 21%;
  width: auto !important;
  background-color: rgb(40, 44, 52);
  border-color: white;
  text-align: center;
  color: white;
  font-size: 21px;
  font-weight: bolder;
}

button:hover {
  background-color: white;
  color: rgb(40, 44, 52);
}
