.parent-task {
    margin-top: 2%;
    width: 100%;
}

.task {
    cursor: pointer;
    box-sizing: border-box;
    padding: 7px 0px 7px 7px;
    text-align: left;
    margin: auto;
    width: 50%;
    border: 1px solid white;
    margin-bottom: 1.2%;
    border-radius: 7px;
    display: flex;
    justify-content: space-between;
}

.completed-task {
    cursor: pointer;
    box-sizing: border-box;
    padding: 7px 0px 7px 7px;
    text-align: left;
    margin: auto;
    width: 50%;
    border: 1px solid white;
    margin-bottom: 1.2%;
    border-radius: 7px;
    display: flex;
    justify-content: space-between;
    box-shadow: -2px 2px 3px 2px whitesmoke;
}

.completed-task>.task-text {
    text-decoration: line-through;
}

.task-options {
    width: 21%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.up-arrow,
.down-arrow,
.cross {
    cursor: pointer;
}